.affairs_inner {
	padding: 50px;
	max-width: 330px;
	display: flex;
	flex-direction: column;
	.filter_wrap {
		display: flex;
		justify-content: space-between;
		align-items: center;
		&:last-child {
			margin-top: 15px;
		}

		.btn {
			padding: 5px 15px;
			font-size: 14px;
			position: relative;
			user-select: none;
			color: #fff;
			background: blueviolet;
			border: 0;
			box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.3);
			transition: all 200ms ease-in-out;
			cursor: pointer;
			overflow: hidden;

			&:hover {
				background-color: #16db47;
				box-shadow: 0px 6px 8px -3px rgba(0, 0, 0, 0.3);
				transform: translateY(-5px);
			}
		}
	}
}



